let Kakao = window.Kakao

const KAKAO_JS_KEY = process.env.REACT_APP_KAKAO_JS_KEY
const SDK_URL = 'https://developers.kakao.com/sdk/js/kakao.js'

//  SDK 
export function getKakaoSdk() {
  const script = document.createElement('script')
  script.src = SDK_URL
  script.onload = initKakaoSdk
  script.onError = () => {
    console.error('Kakao SDK  ')
  }
  document.body.append(script)
}

// SDK   callback . 10     
function waitSdk(callback) {
  let to
  const itv = setInterval(() => {
    if (window.Kakao) {
      Kakao = window.Kakao
      callback()
      clearInterval(itv)
      clearTimeout(to)
    }
  }, 500)
  to = setTimeout(() => {
    console.error('Kakao SDK  ')
    clearInterval(itv)
    clearTimeout(to)
  }, 10 * 1000)
}

// SDK     
export function initKakaoSdk() {
  waitSdk(() => {
    Kakao.init(KAKAO_JS_KEY)
    if (Kakao.isInitialized()) console.log('Kakao SDK  ')
    if (!Kakao.isInitialized()) console.error('Kakao SDK  ')
  })
}

//     
export function kakaoAppLogin(callback, errorCallback) {
  waitSdk(() => {
    Kakao.Auth.login({
      success(res) {
        console.log(' api ', res)
        callback && callback()
      },
      fail(error) {
        console.error('kakao login fail', error)
        errorCallback && errorCallback()
      },
    })
  })
}

//   
export const getKakaoUser = () => {
  return new Promise((resolve, reject) => {
    waitSdk(() => {
      Kakao.API.request({
        url: '/v2/user/me',
        success(response) {
          resolve(response)
        },
        fail(error) {
          console.error(error)
          reject(error)
        },
      })
    })
  })
}
